<template>
  <div class="login">
    <div class="login-form">
      <div class="login-header">
        <p>{{ $Config.siteName }}</p>
      </div>
      <el-input placeholder="请输入用户名" suffix-icon="fa fa-user" v-model="userNmae" style="margin-bottom: 18px"></el-input>
      <el-input placeholder="请输入密码" suffix-icon="fa fa-keyboard-o" v-model="password" type="password" style="margin-bottom: 18px" @keyup.native.enter="login"></el-input>
      <el-button type="primary" :loading="loginLoading" style="width: 100%;margin-bottom: 18px" @click.native="login">登录</el-button>
      <div>
        <el-checkbox v-model="Remenber">Remenber</el-checkbox>
      </div>
    </div>
  </div>
</template>

<script>
  import {
    setToken
  } from "../../utils/dataStorage"
  export default {
    data() {
      return {
        userNmae: "",
        password: "",
        Remenber: true,
        loginLoading: false
      };
    },
    methods: {
      login() {
        this.loginLoading = true
        setTimeout(() => {
          if (this.userNmae === 'admin' && this.password === 'zfkj!@3456') {
            setToken("123456789")
            this.$notify({
              title: "登录成功",
              message: "很高兴你使用后台管理系统！",
              type: "success"
            });
            this.loginLoading = false;
            this.$router.push({
              path: "/"
            });
          } else {
            this.$message.error('账号不存在或者密码输入有误！')
            this.loginLoading = false
          }
        }, 1000);
      }
    }
  };
</script>

<style lang="scss">
  .login {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    height: 100%;
    width: 100%;
    background-color: #4498c4;
    .login-form {
      width: 375px;
      height: 300px;
      padding: 30px;
      background-color: white;
      text-align: left;
      border-radius: 4px;
      position: relative;
      margin-left: 0;
      margin-right: 0;
      zoom: 1;
      display: block;
      .login-header {
        text-align: center;
        font-size: 16px;
        font-weight: bold;
        margin-bottom: 20px;
      }
    }
  }
</style>



